@import './base/reset';
@import './base/vars';
body {
  background: linear-gradient(rgba(46, 46, 46, 0.45), rgba(46, 46, 46, 0.45)), linear-gradient(rgba(0, 0, 0, 1), rgba(0, 0, 0, 1));
  color: var(--white);
  font-family: var(--font-main);
  font-size: var(--font-size);
}


#root{

  width: 100%;
}
.container{
  margin: 0 auto;
  max-width: 1248px;
  padding: 15px;
}
.section-title{
  font-family: var(--font-accent);
  font-size: var(--sections-size);
  padding-bottom: 40px;
  border-bottom: 1px solid white;
  margin-bottom: 40px;
  text-transform: uppercase;
  @media screen and (max-width: 1200px){
    font-size: 64px;
  }
}

