.contact-btn{
  display: inline-block;
  color: var(--white);
  font-size: calc(var(--sections-size) / 4);
  padding: 15px 20px;
  border: 1px solid var(--white);
  border-radius: var(--border-radius);
  text-transform: uppercase;
  transition: all 1s ease;
  &:hover{
    transform: scale(1.1);
}
}