.hero{
  padding-bottom: 101px;
  overflow: hidden;
  &__container{
    margin: 0 auto;
    max-width: 1248px;
    padding: 15px;
    position: relative;
    @media screen and (max-width: 1200px){
      display: flex;
      flex-direction: column;
    }
  }
  &__picture{
    position: absolute;
    bottom: -100%;
    right: 0;
    @media screen and (max-width: 1260px) {
      max-width: 750px;
    }
    @media screen and (max-width: 1200px){
     position: static;
     margin-bottom: -500px;
    }
  }
  &__name, &__job{
    line-height: 100%;
    margin-bottom: 20px;
    padding-bottom: 20px;
  }
  &__job.section-title{
    font-size: 86px;
  }
  &__description{
    margin-bottom: 40px;
    font-size: 24px;
    max-width: 80%;
  }
  &__content{
    max-width: 742px;
  }
}