.marquee{background-color:var(--marquee-grey);margin-bottom:80px}.marquee__content{overflow:hidden}.marquee__wrapper{display:flex;gap:29px;padding:22.5px;animation:move 20s linear infinite}.marquee__text{font-size:calc(var(--sections-size)/4);text-transform:uppercase;white-space:nowrap;position:relative;padding-right:62px}.marquee__text::after{content:"✦";position:absolute;right:0;bottom:2.5%}@keyframes move{0%{transform:translateX(0)}100%{transform:translateX(-100%)}}