
.contact{
  padding-bottom: 100px;
  &__wrapper{
    display: flex;
    @media screen and (max-width: 1000px){
      flex-direction: column;
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title{
    border: none;
    margin-bottom: 20px;
    padding-bottom: 20px;
  }
  &__description{
    text-align: center;
    text-transform: uppercase;
    font-size: 40px;
    margin-bottom: 40px;
  }

}
.mesh{
  position: relative;
  &::after{
    opacity: 0;
    border: 1px solid var(--white);
    content: "Hey, I'm David by Michelangelo. Nice to meet you!";
    color: var(--white);
    font-size: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    padding: 0 15px;
    right: 20%;
    bottom: 15%;
    border-radius: var(--border-radius);
    width: fit-content;
    height: 30px;
    transition: all 2s ease;
  }
  &:hover::after{
    transform: translateY(-5px);
    opacity: 1;
  }
}