.header{
  &__row{
    padding: 40px 0;
    display: flex;
    align-items: center;
    border-bottom: 1px solid white;
  }
  &__name{
    margin-right: 215px;
    font-size: 24px;
    text-transform: uppercase;
    font-family: var(--font-accent);
  }
  &__nav{
    display: flex;
    list-style: none;
    gap: 40px;
    align-items: center;
  }
  &__nav-link{
    font-size: 25px;
    color: var(--white);
    text-decoration: none;
    font-family: var(--font-main);
    border-bottom: 1px solid transparent;
    transition: all 1s ease;
    &:hover{
      border-bottom: 1px solid white;
    }
  }
}
