
.skills{
  margin-bottom: 50px;
  &__title{
    line-height: 100%;
    margin-bottom: 100px;
  }
  &__card{
    padding: 20px 40px;
    font-size: 40px;
    border: 1px solid white;
    border-radius: 56px;
    width: fit-content;
    background: linear-gradient(rgba(255, 255, 255, 0.11),
    rgba(58, 58, 58, 0.11));
    transition: all 1s ease;
    &:hover{
      transform: scale(1.1)
    }
  }
  &__row{
    display: flex;
    gap: 40px;
    margin-bottom: 40px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 75px;
  }
}
