.about{margin-bottom:70px}.about__greetings{font-family:var(--font-accent);font-size:var(--sections-size);line-height:100%}.about__content{display:flex;gap:40px}@media screen and (max-width: 1000px){.about__content{max-width:80%;flex-direction:column}}.about .name{font-weight:600}.about .github{margin-top:15px;color:var(--white);text-decoration:underline;padding-left:45px;position:relative;display:inline-block;transition:all 2s ease}.about .github:hover{transform:translateX(50px)}.about .github::after{content:"→";display:block;position:absolute;left:0;top:50%;transform:translateY(-50%)}